"use client";

import React, { useEffect, useState } from "react";
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card";
import {
    Table,
    TableBody,
    TableCell,
    TableHead,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { Calendar, Clock, Users, DollarSign, Loader2 } from "lucide-react";
import { fetchBookingsByStudio } from "@/actions/admin";
import { format, parseISO, isFuture, isPast, isToday } from "date-fns";

interface Booking {
    id: string;
    userId: string;
    studioId: string;
    roomName?: string;
    date: string;
    startTime: string;
    durationHours: number;
    userCount: number;
    totalPrice: number;
    createdAt: string;
}

export function StudioBookings({ studioId }: { studioId: string }) {
    const [bookings, setBookings] = useState<Booking[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        const loadBookings = async () => {
            setIsLoading(true);
            const data = await fetchBookingsByStudio(studioId);
            setBookings(data);
            setIsLoading(false);
        };
        loadBookings();
    }, [studioId]);

    const getBookingStatus = (date: string) => {
        const bookingDate = parseISO(date);
        if (isToday(bookingDate)) {
            return { label: "今日", color: "bg-cyan-900/30 text-cyan-400 border-cyan-500/30" };
        } else if (isFuture(bookingDate)) {
            return { label: "予約済み", color: "bg-green-900/30 text-green-400 border-green-500/30" };
        } else {
            return { label: "完了", color: "bg-gray-700/30 text-gray-400 border-gray-500/30" };
        }
    };

    if (isLoading) {
        return (
            <Card className="bg-slate-900 border-white/10">
                <CardContent className="py-20 text-center text-gray-500 flex items-center justify-center gap-2">
                    <Loader2 className="animate-spin h-4 w-4" />
                    読み込み中...
                </CardContent>
            </Card>
        );
    }

    const upcomingBookings = bookings.filter((b) => isFuture(parseISO(b.date)) || isToday(parseISO(b.date)));
    const pastBookings = bookings.filter((b) => isPast(parseISO(b.date)) && !isToday(parseISO(b.date)));
    const totalRevenue = bookings.reduce((sum, b) => sum + b.totalPrice, 0);

    return (
        <div className="space-y-6">
            {/* Stats */}
            <div className="grid md:grid-cols-4 gap-4">
                <Card className="bg-slate-900 border-white/10">
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium text-gray-400">
                            総予約数
                        </CardTitle>
                        <Calendar className="h-4 w-4 text-cyan-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-white">{bookings.length}</div>
                    </CardContent>
                </Card>

                <Card className="bg-slate-900 border-white/10">
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium text-gray-400">
                            今後の予約
                        </CardTitle>
                        <Clock className="h-4 w-4 text-green-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-green-400">
                            {upcomingBookings.length}
                        </div>
                    </CardContent>
                </Card>

                <Card className="bg-slate-900 border-white/10">
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium text-gray-400">
                            過去の予約
                        </CardTitle>
                        <Calendar className="h-4 w-4 text-gray-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-gray-400">
                            {pastBookings.length}
                        </div>
                    </CardContent>
                </Card>

                <Card className="bg-slate-900 border-white/10">
                    <CardHeader className="flex flex-row items-center justify-between pb-2">
                        <CardTitle className="text-sm font-medium text-gray-400">
                            総売上
                        </CardTitle>
                        <DollarSign className="h-4 w-4 text-cyan-400" />
                    </CardHeader>
                    <CardContent>
                        <div className="text-2xl font-bold text-cyan-400">
                            ¥{totalRevenue.toLocaleString()}
                        </div>
                    </CardContent>
                </Card>
            </div>

            {/* Bookings Table */}
            <Card className="bg-slate-900 border-white/10">
                <CardHeader>
                    <CardTitle className="text-cyan-400">予約一覧</CardTitle>
                </CardHeader>
                <CardContent className="p-0">
                    <Table>
                        <TableHeader className="bg-white/5">
                            <TableRow className="border-white/10 hover:bg-transparent">
                                <TableHead className="text-cyan-400">予約ID</TableHead>
                                <TableHead className="text-cyan-400">日付</TableHead>
                                <TableHead className="text-cyan-400">時間</TableHead>
                                <TableHead className="text-cyan-400">部屋</TableHead>
                                <TableHead className="text-cyan-400">時間</TableHead>
                                <TableHead className="text-cyan-400">人数</TableHead>
                                <TableHead className="text-cyan-400">金額</TableHead>
                                <TableHead className="text-cyan-400">ステータス</TableHead>
                            </TableRow>
                        </TableHeader>
                        <TableBody>
                            {bookings.length === 0 ? (
                                <TableRow>
                                    <TableCell
                                        colSpan={8}
                                        className="text-center py-10 text-gray-500"
                                    >
                                        予約がありません
                                    </TableCell>
                                </TableRow>
                            ) : (
                                bookings.map((booking) => {
                                    const status = getBookingStatus(booking.date);
                                    return (
                                        <TableRow
                                            key={booking.id}
                                            className="border-white/10 hover:bg-white/5"
                                        >
                                            <TableCell className="font-mono text-sm text-gray-400">
                                                {booking.id.substring(0, 8)}...
                                            </TableCell>
                                            <TableCell className="text-white">
                                                {format(parseISO(booking.date), "yyyy/MM/dd")}
                                            </TableCell>
                                            <TableCell className="text-white">
                                                {booking.startTime}
                                            </TableCell>
                                            <TableCell className="text-white">
                                                {booking.roomName || "-"}
                                            </TableCell>
                                            <TableCell className="text-white">
                                                {booking.durationHours}時間
                                            </TableCell>
                                            <TableCell className="text-white">
                                                <div className="flex items-center gap-1">
                                                    <Users className="h-3 w-3 text-gray-400" />
                                                    {booking.userCount}
                                                </div>
                                            </TableCell>
                                            <TableCell className="font-medium text-cyan-400">
                                                ¥{booking.totalPrice.toLocaleString()}
                                            </TableCell>
                                            <TableCell>
                                                <Badge className={status.color}>{status.label}</Badge>
                                            </TableCell>
                                        </TableRow>
                                    );
                                })
                            )}
                        </TableBody>
                    </Table>
                </CardContent>
            </Card>
        </div>
    );
}
