"use server";

import { getBookings } from "@/lib/db-local";

export async function fetchBookings() {
    // In a real server component, we can just call the DB function directly.
    // Or expose it as a server action if we need client-side fetching.
    return getBookings().sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
}

export async function fetchBookingsByStudio(studioId: string) {
    const allBookings = getBookings();
    return allBookings
        .filter(booking => booking.studioId === studioId)
        .sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
}
